import React, { useEffect } from 'react';
import { Card, CardActions, CardContent, Dialog, Button } from '@material-ui/core';
import { AppBar, Toolbar, IconButton, Typography } from '@material-ui/core';
import { Table, TableHead, TableBody, TableRow, TableCell, Grid, Divider } from '@material-ui/core'
import { Delete, Edit, Payment } from '@material-ui/icons'
import CloseIcon from '@material-ui/icons/Close';
import { Autocomplete } from '@material-ui/lab';

import { ContainerInput, ContainerCard, ContainerDialog } from './styles';

interface Item {
  ID: number,
  IDPRODUTO: string,
  DESCPRD: string,
  QUANTIDADE: number,
  VALOR: number,
}

interface OpcaoPagamento {
  ID: number,
  DESCRICAO: string,
}

interface ItemEdit {
  quantidade: number;
  idProduto: string;
}

interface Itens {
  itens: Array<Item>,
  total: number;
  pagamento: { ID: number, DESCRICAO: string };
  opcaoPagamento: Array<OpcaoPagamento>,
  setPagamento: (pagamento: { ID: number, DESCRICAO: string }) => void,
  setTotal: (total: number) => void,
  setItens: (itens: Array<Item>) => void,
  setEditItem: (value: ItemEdit) => void,
}

const CardItem: React.FC<Itens> = ({ itens, setItens, total, setTotal, opcaoPagamento, pagamento, setPagamento, setEditItem }) => {
  const [dialogOpen, setDialogOpen] = React.useState(false);
  const [isFocused,] = React.useState(false);

  useEffect(() => {
    const handleTotal = () => {
      setTotal(itens.reduce((acc, cur) => acc + (cur.QUANTIDADE * cur.VALOR), 0))
    }

    handleTotal()
  }, [itens, setTotal])

  const handleDelete = (idProduto: string) => {
    const newItens = itens.filter(({ IDPRODUTO }) => idProduto !== IDPRODUTO)
    const [totalFiltered] = itens.filter(({ IDPRODUTO }) => idProduto === IDPRODUTO)

    setTotal(total - (totalFiltered.QUANTIDADE * totalFiltered.VALOR));
    setItens(newItens)
  }

  const handleEdit = (idProduto: string, quantidade: number) => {
    setEditItem({ idProduto, quantidade });
  }

  const handleChangePagamento = (e: any, value: any) => {
    if (value) {
      setPagamento({ ID: value.ID, DESCRICAO: value.DESCRICAO });
    }
  }

  const handleShowItens = () => {
    dialogOpen ? setDialogOpen(false) : setDialogOpen(true)
  }

  return (
    <>
      <ContainerCard>
        <Card onClick={handleShowItens}>
          <CardContent style={{ overflowX: 'scroll', maxWidth: '95vw' }}>
            <div>
              <Table size="small" >
                <TableHead>
                  <TableRow>
                    <TableCell style={{ fontWeight: 'bold' }} >DESC</TableCell>
                    <TableCell style={{ fontWeight: 'bold', textAlign: 'right' }} >QTD</TableCell>
                    <TableCell style={{ fontWeight: 'bold', textAlign: 'right' }} >UNIT.</TableCell>
                    <TableCell style={{ fontWeight: 'bold', textAlign: 'right' }} >TOTAL</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {itens.map((row) => (
                    <TableRow key={row.IDPRODUTO}>
                      <TableCell>{row.DESCPRD}</TableCell>
                      <TableCell style={{ textAlign: 'right' }}>{row.QUANTIDADE}</TableCell>
                      <TableCell style={{ textAlign: 'right' }}>{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR)}</TableCell>
                      <TableCell style={{ textAlign: 'right' }}>{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR * row.QUANTIDADE)}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </CardContent>
          <CardActions>
            <Typography color="textSecondary" variant="body2">
              {new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(total)}
            </Typography>
          </CardActions>
        </Card >
      </ContainerCard>

      <ContainerDialog>
        <Dialog fullScreen open={dialogOpen} >
          <AppBar style={{ background: '#312E38' }}>
            <Toolbar>
              <IconButton edge="start" color="inherit" onClick={handleShowItens} aria-label="close">
                <CloseIcon />
              </IconButton>
              <Typography variant="h6">
                Itens do pedido
            </Typography>
            </Toolbar>
          </AppBar>

          <Grid container alignItems="center">
            <Grid item>
              <Typography gutterBottom variant="h6">
                Total {new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(total)}
              </Typography>
            </Grid>
          </Grid>

          <Divider variant="middle" />

          <div style={{ marginTop: 20 }}>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">Produto</TableCell>
                  <TableCell align="left">Descrio</TableCell>
                  <TableCell align="center">Qtd</TableCell>
                  <TableCell align="center">Valor</TableCell>
                  <TableCell align="center">Total</TableCell>
                  <TableCell align="left">Aes</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {itens.map((row) => (
                  <TableRow key={row.IDPRODUTO}>
                    <TableCell align="left">{row.IDPRODUTO}</TableCell>
                    <TableCell align="left">{row.DESCPRD}</TableCell>
                    <TableCell align="right">{row.QUANTIDADE}</TableCell>
                    <TableCell align="right">{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR)}</TableCell>
                    <TableCell align="right">{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR * row.QUANTIDADE)}</TableCell>
                    <TableCell align="left" >
                      <Button onClick={() => handleDelete(row.IDPRODUTO)}><Delete /></Button>
                      <Button onClick={() => handleEdit(row.IDPRODUTO, row.QUANTIDADE)}><Edit /></Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </Dialog>
      </ContainerDialog>

      <ContainerInput isFocused={isFocused} >
        <Autocomplete
          id="combo-box"
          options={opcaoPagamento}
          getOptionLabel={(option) => option.DESCRICAO}
          renderInput={(params) =>
            <div ref={params.InputProps.ref} style={{ display: 'flex', color: '#666360' }}>
              <Payment />
              <input type="text" {...params.inputProps} />
            </div>
          }
          onChange={handleChangePagamento}
          getOptionSelected={(option, value) => option.DESCRICAO === value.DESCRICAO}
          value={pagamento}
        />
      </ContainerInput>
    </>
  );
}

export default CardItem;
