import React, { useCallback, useRef } from 'react';
import { FiLock, FiUser } from 'react-icons/fi';
import { FormHandles } from '@unform/core';
import { Form } from '@unform/web';
import * as Yup from 'yup';
import getValidantionErrors from '../../utils/getValidationErrors';

import { useAuth } from '../../hooks/Auth';
import { useToast } from '../../hooks/Toast';

import { Container, Content } from './styles';
import Button from '../../components/Button';
import Input from '../../components/Input';
import logo from '../../assets/NewLogoWhite.png';

interface SignInFormData {
  login: string;
  pass: string;
}
const SignIn: React.FC = () => {
  const formRef = useRef<FormHandles>(null);

  const { signIn } = useAuth();
  const { addToast } = useToast();

  const handleSubmit = useCallback(
    async (data: SignInFormData) => {
      try {
        formRef.current?.setErrors({});
        const schema = Yup.object().shape({
          pass: Yup.string().required('Password obrigatrio'),
          login: Yup.string()
            .required('login  obrigatrio')
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        await signIn({
          login: data.login,
          pass: data.pass,
        });

      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidantionErrors(err);
          formRef.current?.setErrors(errors);

          return;
        }

        addToast({
          type: 'error',
          title: 'Erro na autenticao',
          description:
            'Ocorreu um erro ao fazer login, verifique as credenciais.',
        });
      }
    },
    [signIn, addToast],
  );
  return (
    <Container>
      <Content>
        <img src={logo} alt="Logo Monarca" />
        <Form ref={formRef} onSubmit={handleSubmit}>
          <h1>Faa seu Login</h1>
          <Input name="login" icon={FiUser} placeholder="Login" style={{'textTransform': 'uppercase'}} />
          <Input
            name="pass"
            icon={FiLock}
            type="password"
            placeholder="Password"
            style={{'textTransform': 'uppercase'}}
          />
          <Button type="submit">Entrar</Button>
        </Form>
        <div style={{ color: '#f4edeb' }}>Copyright  2021 </div>
      </Content>
    </Container>
  );
};
export default SignIn;
