import styled from 'styled-components';

interface Props {
  placement?: string;
}

export const Container = styled.div<Props>`
  position: relative;

  span {
    width: 180px;
    background: #dc6a48;
    padding: 8px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    color: #312e38;
    opacity: 0;
    transition: opacity 0.4s;
    visibility: hidden;
    text-align: center;

    position: absolute;

    @media only screen and (min-width: 992px) {
      ${(props) =>
        props.placement === 'top'
          ? 'bottom: calc(100% + 8px);'
          : 'top: calc(100% + 8px);'}

      left: 50%;
      transform: translateX(-50%);
    }

    @media only screen and (max-width: 992px) {
      bottom: calc(50% + 18px);
      right: -6px;
      /*transform: translateX(-50%);*/
    }

    &::before {
      content: '';
      border-style: solid;
      border-color: #dc6a48 transparent;

      position: absolute;

      ${(props) =>
        props.placement === 'top'
          ? 'top: 100%; border-width: 6px 6px 0 6px;'
          : 'bottom: 100%; border-width: 0 6px 6px 6px;'}

      @media only screen and (min-width: 992px) {
        left: 50%;
        transform: translateX(-50%);
      }
      @media only screen and (max-width: 992px) {
        right: 10px;
        /*right: 50%;*/
        /*transform: translateX(-50%);*/
      }
    }
  }

  &:hover span {
    opacity: 1;
    visibility: visible;
  }
`;
