import React, { useRef, useCallback } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Form } from '@unform/web';
import { useAuth } from '../../hooks/Auth';
import { FormHandles } from '@unform/core';
import { FiEdit, FiPlus, FiCalendar } from 'react-icons/fi';
import Stepper from '../../components/Stepper';
import Input from '../../components/Input';
import Button from '../../components/Button';
import { api } from '../../services/api';
import Table from '../../components/Table';
import { format, isBefore, isValid } from 'date-fns';
import logo from '../../assets/logoWhite.png';
import {
  Container,
  Content,
  Header,
  ContainerButtons,
  ContainerCheckout,
} from './styles'
import CardItem from '../../components/CardItem';
import AddItem from '../../components/AddItem';
import { useToast } from '../../hooks/Toast';
import { Typography, TextField } from '@material-ui/core';

interface Edit {
  quantidade: number;
  idProduto: string;
}

interface Item {
  ID: number,
  IDPRODUTO: string,
  DESCPRD: string,
  QUANTIDADE: number,
  VALOR: number,
}

const Order: React.FC = () => {
  const formRef = useRef<FormHandles>(null);
  const { addToast } = useToast();
  const history = useHistory();
  const { order, editOrder } = useAuth();
  const [idCustomer, setIdCustomer] = React.useState('');
  const [activeStep, setActiveStep] = React.useState(0);
  const [cpfCnpj, setcpfCnpj] = React.useState('');
  const [apelido, setApelido] = React.useState('');
  const [telefone, setTelefone] = React.useState('');
  const [fax, setFax] = React.useState('');
  const [cep, setCep] = React.useState('');
  const [endereco, setEndereco] = React.useState('');
  const [bairro, setBairro] = React.useState('');
  const [numero, setNumero] = React.useState('');
  const [dataEntrega, setdataEntrega] = React.useState(format(new Date(), 'yyyy-MM-dd'));
  const [total, setTotal] = React.useState(0);
  const [openAdd, setOpenAdd] = React.useState(false)
  const [comboItens, setComboItens] = React.useState([]);
  const [disabledNext, setDisabledNext] = React.useState(true)
  const [itens, setItens] = React.useState<Item[]>([]);
  const [opcaoPagamento, setOpcaoPagamento] = React.useState([]);
  const [observacao, setObservacao] = React.useState('');

  const [pagamento, setPagamento] = React.useState({ ID: 0, DESCRICAO: '' });
  const [editItem, setEditItem] = React.useState<Edit>({
    quantidade: 0,
    idProduto: ''
  });

  const [cpfCnpjDisabled, setCpfCnpjDisabled] = React.useState(false);

  const token = localStorage.getItem('@Monarca: token');

  const handleFormat = useCallback((e): void => {
    let input = e.target.value;
    input = input
      .replace(/\D/g, '')
      .replace(/(\d{3})(\d)/, '$1.$2')
      .replace(/(\d{3})(\d)/, '$1.$2')
      .replace(/(\d{3})(\d{1,2})/, '$1-$2')
      .replace(
        /(\d{2})(\d{1}).(\d{2})(\d{1}).(\d{2})(\d{1})-(\d{3})/,
        '$1.$2$3.$4$5/$6$7',
      )
      .replace(/(\/\d{4})(\d{1,2})/, '$1-$2')
      .replace(/(-\d{2})\d+?$/, '$1');

    setcpfCnpj(input);
  }, []);

  const handleFormatPhone = useCallback((e): void => {
    let input = e.target.value;
    input = input
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '($1) $2')
      .replace(/(\d{4})(\d)/, '$1-$2')
      .replace(/(\d{4})-(\d)(\d{4})/, '$1$2-$3')
      .replace(/(-\d{4})\d+?$/, '$1');

    setTelefone(input)
  }, []);

  const handleFormatFax = useCallback((e): void => {
    let input = e.target.value;
    input = input
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '($1) $2')
      .replace(/(\d{4})(\d)/, '$1-$2')
      .replace(/(-\d{4})\d+?$/, '$1');

    setFax(input)
  }, []);

  const handleFormatCep = useCallback((e): void => {
    let input = e.target.value;
    input = input
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '$1.$2')
      .replace(/(\d{3})(\d)/, '$1-$2')
      .replace(/(-\d{3})\d+?$/, '$1');

    setCep(input)
  }, []);

  const handleVerify = useCallback((): void => {
    const cpfCnpjInput = formRef.current?.getFieldRef('cpfCnpj');
    const bairroInput = formRef.current?.getFieldRef('bairro');
    const enderecoInput = formRef.current?.getFieldRef('endereco');
    const telefoneInput = formRef.current?.getFieldRef('telefone');
    const NumeroInput = formRef.current?.getFieldRef('numero');
    const CepInput = formRef.current?.getFieldRef('cep');

    let cpfCnpjValue = cpfCnpjInput.value;
    cpfCnpjValue = cpfCnpjValue.replace(/\D/g, '');

    if (cpfCnpjValue.length === 14 || cpfCnpjValue.length === 11) {

      api.get(`/customer/search?cpfCnpj=${cpfCnpjValue}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(response => {
          if (response.data.customer) {
            const {
              ID,
              APELIDO,
              BAIRRO_CADMOB,
              CEP_CADMOB,
              ENDEER_CADMOB,
              FAX,
              FONE,
              NUMERO_CADMOB
            } = response.data.customer;
            setIdCustomer(ID)
            setApelido(APELIDO);
            setTelefone(FONE ? FONE : '');
            setFax(FAX ? FAX : '');
            setCep(CEP_CADMOB);
            setEndereco(ENDEER_CADMOB);
            setBairro(BAIRRO_CADMOB);
            setNumero(NUMERO_CADMOB);
            enderecoInput.focus();
            bairroInput.focus();
            telefoneInput.focus();
            NumeroInput.focus();
            CepInput.focus();
          }
          setComboItens(response.data.products);
          setOpcaoPagamento(response.data.payments);

        })
        .catch(e => {
          console.log(e)
        })
    } else if (cpfCnpjInput) {
      cpfCnpjInput.focus();
    }
  }, [token]);

  const handleSubmit = () => {

  }

  const handleOpenAdd = () => {
    setOpenAdd(true)
  }

  const handleSave = () => {
    const customerSave = { id: idCustomer, cpfCnpj, apelido, fone: telefone, fax, cep, endereco, bairro, numero }

    if (!order.id) {
      const orderSave = { formapgto: pagamento.ID, ocorrencia: '50', entrega: dataEntrega, itens, observacao }

      const data = { customer: customerSave, order: orderSave }


      api.post(`/orders/insert`, data, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(({ data }) => {
          addToast({
            type: 'success',
            title: 'Sucesso',
            description:
              data.msg,
          });
        })
        .catch(() => {
          addToast({
            type: 'error',
            title: 'Erro',
            description:
              'Falha ao cadastar o pedido.',
          });
        })
        .finally(() => {
          history.push('/dashboard');
        });
    }
    else {
      const orderSave = { id: order.id, formapgto: pagamento.ID, ocorrencia: '50', entrega: dataEntrega, itens }

      const data = { customer: customerSave, order: orderSave }

      api.put(`/orders/update`, data, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(({ data }) => {
          addToast({
            type: 'success',
            title: 'Sucesso',
            description:
              data.msg,
          });
          history.push('/dashboard');
        })
        .catch(() => {
          addToast({
            type: 'error',
            title: 'Erro',
            description:
              'Falha ao editar o pedido.',
          });
        })
        .finally(() => {
          editOrder({
            cpfCnpj: '',
            pedido: 0,
            itens: [],
            status: '',
            id: 0,
            pagamento: { ID: 0, DESCRICAO: '' },
            entrega: '',
          })
          history.push('/dashboard');
        });
    }
  }

  const handleSend = () => {
    const customerSave = { id: idCustomer, cpfCnpj, apelido, fone: telefone, fax, cep, endereco, bairro, numero }

    if (!order.id) {
      const orderSave = { formapgto: pagamento.ID, ocorrencia: '51', entrega: dataEntrega, itens, observacao }

      const data = { customer: customerSave, order: orderSave }

      api.post(`/orders/insert`, data, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(({ data }) => {
          addToast({
            type: 'success',
            title: 'Sucesso',
            description:
              data.msg,
          });
        })
        .catch(() => {
          addToast({
            type: 'error',
            title: 'Erro',
            description:
              'Falha ao cadastar o pedido.',
          });
        })
        .finally(() => {
          history.push('/dashboard');
        });
    }
    else {
      const orderSave = { id: order.id, formapgto: pagamento.ID, ocorrencia: '51', entrega: dataEntrega, itens }

      const data = { customer: customerSave, order: orderSave }

      api.put(`/orders/update`, data, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(({ data }) => {
          addToast({
            type: 'success',
            title: 'Sucesso',
            description:
              data.msg,
          });
        })
        .catch(() => {
          addToast({
            type: 'error',
            title: 'Erro',
            description:
              'Falha ao editar o pedido.',
          });
        })
        .finally(() => {
          editOrder({
            cpfCnpj: '',
            pedido: 0,
            itens: [],
            status: '',
            id: 0,
            pagamento: { ID: 0, DESCRICAO: '' },
            entrega: '',
          })
          history.push('/dashboard');
        });
    }
  }

  const handleDataEntrega = (e: any) => {
    if (isValid(new Date(e))) {
      if (isBefore(new Date(e), new Date())) {
        setdataEntrega(format(new Date(), 'yyyy-MM-dd'));
      } else {
        setdataEntrega(e)
      }
    } else {
      setdataEntrega(format(new Date(), 'yyyy-MM-dd'))
    }
  }

  React.useEffect(() => {
    if (activeStep === 0) {
      if (numero !== '' && cpfCnpj !== '' && apelido !== '' && cep !== '' && endereco !== '' && bairro !== '') {
        setDisabledNext(false)
      } else {
        setDisabledNext(true)
      }

    }
    else if (activeStep === 1) {
      (itens.length === 0 || pagamento.ID === 0) ? setDisabledNext(true) : setDisabledNext(false);
    }
    else {
      return setDisabledNext(true);
    }
  }, [activeStep, cpfCnpj, apelido, cep, endereco, bairro, numero, pagamento, itens])


  React.useEffect(() => {
    const cpfCnpjInput = formRef.current?.getFieldRef('cpfCnpj');
    const apelidoInput = formRef.current?.getFieldRef('apelido');
    const bairroInput = formRef.current?.getFieldRef('bairro');
    const enderecoInput = formRef.current?.getFieldRef('endereco');
    const telefoneInput = formRef.current?.getFieldRef('telefone');

    if (order.cpfCnpj) {
      setcpfCnpj(order.cpfCnpj);
      api.get(`/customer/search?cpfCnpj=${order.cpfCnpj}`, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
        .then(response => {
          if (response.data.customer) {
            const {
              ID,
              APELIDO,
              BAIRRO_CADMOB,
              CEP_CADMOB,
              ENDEER_CADMOB,
              FAX,
              FONE,
              NUMERO_CADMOB
            } = response.data.customer;

            setCpfCnpjDisabled(true);
            setIdCustomer(ID);
            setApelido(APELIDO);
            setTelefone(FONE ? FONE : '');
            setFax(FAX ? FAX : '');
            setCep(CEP_CADMOB);
            setEndereco(ENDEER_CADMOB);
            setBairro(BAIRRO_CADMOB);
            setNumero(NUMERO_CADMOB);
            apelidoInput.focus();
            enderecoInput.focus();
            bairroInput.focus();
            telefoneInput.focus();
          }
          setComboItens(response.data.products);
          setOpcaoPagamento(response.data.payments);
          setdataEntrega(format(new Date(order.entrega), 'yyyy-MM-dd'));

          const itens_modificados = order.itens.map(value => {
            return {
              ID: value.IDITEM,
              IDPRODUTO: value.IDPRODUTO,
              DESCPRD: value.DESCPRD,
              QUANTIDADE: value.QTDADE,
              VALOR: value.VR_UNITARIO,
            }
          })
          setItens(itens_modificados);
          setPagamento(order.pagamento);

          const total_itens = itens_modificados.reduce((acc, value) => {
            acc += (value.VALOR * value.QUANTIDADE);
            return acc;
          }, 0)
          setTotal(total_itens);

        })
        .catch(e => {
          console.log(e)
        })
    } else if (cpfCnpjInput) {
      cpfCnpjInput.focus();
    }
  }, [order, token])


  return (
    <Container>
      <Header>
        <img src={logo} alt="logo Monarca" />
        <Link to="/dashboard">
          <Button style={{ width: 150 }}>Voltar</Button>
        </Link>
      </Header>
      <Content>
        <Form onSubmit={handleSubmit} ref={formRef}>
          {activeStep === 0 &&
            <>
              <Input type="text" icon={FiEdit} name="cpfCnpj" placeholder="CPF / CNPJ" value={cpfCnpj} onChange={handleFormat} handleVerify={handleVerify} search disabled={cpfCnpjDisabled} />
              <Input type="text" maxLength={20} style={{ 'textTransform': 'uppercase' }} icon={FiEdit} name="apelido" placeholder="APELIDO" value={apelido} onChange={e => (setApelido(e.target.value.toUpperCase()))} />
              <Input type="text" icon={FiEdit} name="telefone" placeholder="TELEFONE" value={telefone} onChange={e => handleFormatPhone(e)} />
              <Input type="text" icon={FiEdit} name="fax" placeholder="FAX" value={fax} onChange={e => handleFormatFax(e)} />
              <Input type="text" icon={FiEdit} name="cep" placeholder="CEP" value={cep} onChange={e => handleFormatCep(e)} />
              <Input type="text" style={{ 'textTransform': 'uppercase' }} icon={FiEdit} name="endereco" placeholder="ENDEREO" value={endereco} onChange={e => (setEndereco(e.target.value.toUpperCase()))} />
              <Input type="text" style={{ 'textTransform': 'uppercase' }} icon={FiEdit} name="bairro" placeholder="BAIRRO" value={bairro} onChange={e => (setBairro(e.target.value.toUpperCase()))} />
              <Input type="text" icon={FiEdit} name="numero" placeholder="NMERO" value={numero} onChange={e => (setNumero(e.target.value))} />
            </>}
          {activeStep === 1 &&
            <>
              <ContainerButtons>
                <div className="containerInput">
                  <Input type="date" icon={FiCalendar} name="dataEntrega" placeholder="DATA DE ENTREGA" value={dataEntrega} onChange={e => handleDataEntrega(e.target.value)} />
                </div>
                <div className="containerButton">
                  <Button onClick={handleOpenAdd} ><FiPlus /></Button>
                </div>
              </ContainerButtons>

              <AddItem
                itens={itens}
                setItens={setItens}
                openAdd={openAdd || !!editItem.idProduto}
                setOpenAdd={setOpenAdd}
                comboItens={comboItens}
                total={total}
                setTotal={setTotal}
                editItem={editItem}
                setEditItem={setEditItem}
              />
              <CardItem
                itens={itens}
                setItens={setItens}
                total={total}
                setTotal={setTotal}
                opcaoPagamento={opcaoPagamento}
                pagamento={pagamento}
                setPagamento={setPagamento}
                setEditItem={setEditItem}
              />
            </>
          }
          {activeStep === 2 &&
            <>
              <ContainerCheckout>
                <div className="containerName">
                  {apelido
                    ? <h4>{apelido}</h4>
                    : <h4>-</h4>
                  }
                  <Typography gutterBottom>
                    {new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(total)}
                  </Typography>
                </div>

                <div className="containerButtons">
                  <Table itens={itens} setItens={setItens} total={total} />
                  {/* <Input type="text" className="textField" name="observacoes" placeholder="OBSERVAES" value={observacao} onChange={(e) => setObservacao(e.target.value)} /> */}
                  <TextField multiline className="textField" onChange={(e) => { setObservacao(e.target.value); console.log(observacao) }} variant="outlined" placeholder="OBSERVAES" />
                  <Button className="button" onClick={handleSave}>Salvar</ Button>
                  <Button className="button" onClick={handleSend}>Salvar e Finalizar</ Button>

                </div>
              </ContainerCheckout>
            </>
          }
        </Form>
        <Stepper setActiveStep={setActiveStep} activeStep={activeStep} disabledNext={disabledNext} />
      </Content>
    </Container>
  )
}

export default Order
