import React, { useCallback, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import * as Yup from 'yup';
import { Form } from '@unform/web';
import { FormHandles } from '@unform/core';
import { FiKey } from 'react-icons/fi';

import { useAuth } from '../../hooks/Auth';
import { useToast } from '../../hooks/Toast';
import getValidantionErrors from '../../utils/getValidationErrors';

import Button from '../../components/Button';
import Input from '../../components/Input';

import {
  Container,
  Content,
  Header,
  Grid,
} from './styles';
import logo from '../../assets/NewLogoWhite.png';

interface FormData {
  numero_serie: string;
}

const Dashboard: React.FC = () => {
  const formRef = useRef<FormHandles>(null);
  const history = useHistory();
  const { changePass, login } = useAuth();
  const { addToast } = useToast();

  const handleSubmit = useCallback(
    async (data: FormData) => {
      try {
        const pass = formRef.current?.getFieldValue('newPassword');

        formRef.current?.setErrors({});
        const schema = Yup.object().shape({
          password: Yup.string()
            .test(
              'password',
              'Senhas no correspondem',
              (value) => value === pass,
            )
            .min(6, 'Mnimo 6 dgitos'),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        if (login) {
          await changePass({login: login, pass: pass})
        }

        addToast({
          type: 'success',
          title: 'Alterao de senha',
          description: 'Nova senha atualizada com sucesso',
        });

        history.push('/dashboard');
      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          const errors = getValidantionErrors(err);
          formRef.current?.setErrors(errors);

          return;
        }

        addToast({
          type: 'error',
          title: 'Erro ao alterar senha',
          description: 'Ocorreu um erro ao alterar a senha.',
        });
      }
    },
    [addToast, history, changePass, login],
  );

  return (
    <>
      <Container>
        <Content>
          <Header>
            <img src={logo} alt="logo Monarca" />
          </Header>
          <Grid>
              <Form ref={formRef} onSubmit={handleSubmit}>
                <h1>Alterar senha</h1>
                <Input
                  type="password"
                  icon={FiKey}
                  name="password"
                  placeholder="Nova senha"
                  maxLength={50}
                  style={{'textTransform': 'uppercase'}}
                />
                <Input
                  type="password"
                  icon={FiKey}
                  name="newPassword"
                  placeholder="Repetir nova senha"
                  maxLength={50}
                  style={{'textTransform': 'uppercase'}}
                />
                <Button type="submit">Salvar</Button>
              </Form>
          </Grid>
          <div style={{ color: '#f4edeb' }}>Copyright  2021 </div>
        </Content>
      </Container>
    </>
  );
};

export default Dashboard;
