import React, { createContext, useCallback, useState, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { api } from '../services/api';

interface AuthContextData {
  nome: string;
  login?: string;
  firstLogin?: boolean;
  signIn(credentials: SignInCredentials): Promise<void>;
  signOut(): void;
  changePass(credentials: SignInCredentials): Promise<void>;
  order: Order;
  editOrder(order: Order): void;
}

interface SignInCredentials {
  login: string;
  pass: string;
}

interface AuthState {
  nome: string;
  token: string;
  login?: string;
  firstLogin?: boolean;
}

interface Items {
  IDITEM: number;
  IDPRODUTO: string;
  DESCPRD: string;
  QTDADE: number;
  VR_UNITARIO: number;
  VR_TOTAL: number;
}

interface Order {
  cpfCnpj: string;
  pedido: number;
  itens: Array<Items>;
  status: string;
  id: number;
  pagamento: { ID: number, DESCRICAO: string};
  entrega: string;
}

const AuthContext = createContext<AuthContextData>({} as AuthContextData);

export const AuthProvider: React.FC = ({ children }) => {
  const history = useHistory();
  const [order, setOrder] = useState<Order>({
    cpfCnpj: '',
    pedido: 0,
    itens: [],
    status: '',
    id: 0,
    pagamento: {ID: 0, DESCRICAO: ''},
    entrega: '',
  });
  const [data, setData] = useState<AuthState>(() => {
    const token = localStorage.getItem('@Monarca: token');
    const nome = localStorage.getItem('@Monarca: nome');

    if (token && nome) {
      return { token, nome };
    }
    return {} as AuthState;
  });

  const signIn = useCallback(async ({ login: userLogin, pass }) => {
    const response = await api.post('access/auth/user', {
      login: userLogin,
      pass,
    });

    const { token, nome, firstLogin, login } = response.data;

    if (firstLogin) {
      setData({ token, nome, login, firstLogin });
      history.push('/password')
    } else {
      localStorage.setItem('@Monarca: token', token);
      localStorage.setItem('@Monarca: nome', nome);

      setData({ token, nome, login, firstLogin });
      history.push('/dashboard')
    }

  }, [history]);

  const changePass = useCallback(async ({ login, pass }) => {
    const response = await api.put('access/auth/pass', {
      login,
      pass,
    });

    const { token, nome } = response.data;

    localStorage.setItem('@Monarca: token', token);
    localStorage.setItem('@Monarca: nome', nome);

    setData({ token, nome });
    history.push('/dashboard')

  }, [history]);

  const signOut = useCallback(() => {
    localStorage.removeItem('@Monarca: token');
    localStorage.removeItem('@Monarca: nome');

    setData({} as AuthState);
  }, []);

  const editOrder = useCallback((order: Order): void => {
    setOrder(order);
  }, []);

  return (
    <AuthContext.Provider value={{ nome: data.nome, login: data.login, firstLogin: data.firstLogin, signIn, signOut, changePass, order, editOrder }}>
      {children}
    </AuthContext.Provider>
  );
};

export function useAuth(): AuthContextData {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error(' necessrio utilizar AuthProvider encapsulando as rotas');
  }

  return context;
}
