import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

interface Itens {
  itens: Array<Item>,
  setItens: (itens: Array<Item>) => void,
  total: number;
}

interface Item {
  ID: number,
  IDPRODUTO: string,
  DESCPRD: string,
  QUANTIDADE: number,
  VALOR: number,
}

const useStyles = makeStyles({
  root: {
    '&.MuiPaper-root': {
      height: '50vh',
    },
  },
  table: {
    '&.MuiTable-root': {
      width: '150%',
    }
  }
});

export default function DenseTable({ itens, total }: Itens) {
  const classes = useStyles();

  return (
    <TableContainer className={classes.root} component={Paper}>
      <Table size="small" className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell width="300px" style={{ fontWeight: 'bold' }}>DESC</TableCell>
            <TableCell width="100px" style={{ fontWeight: 'bold', textAlign: 'right' }}>QTD</TableCell>
            <TableCell width="100px" style={{ fontWeight: 'bold', textAlign: 'right' }}>UNIT.</TableCell>
            <TableCell width="100px" style={{ fontWeight: 'bold', textAlign: 'right' }}>TOTAL</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {itens.map((row, key) => (
            <TableRow key={key}>
              <TableCell style={{ wordBreak: 'break-all' }}>{row.DESCPRD}</TableCell>
              <TableCell style={{ textAlign: 'right' }}>{row.QUANTIDADE}</TableCell>
              <TableCell style={{ textAlign: 'right' }}>{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR)}</TableCell>
              <TableCell style={{ textAlign: 'right' }}>{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VALOR * row.QUANTIDADE)}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
