import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Dialog from '../../components/Dialog';
import { format } from 'date-fns';

const useStyles = makeStyles({
  root: {
    marginBottom: 10,
    cursor: 'pointer',
    width: 300,
    margin: 5,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

export default function SimpleCard({ NUMERO, EMISSAO, CAPELIDO, STATUS, ITENS, TOTAL, OCORRENCIA, CNPJ, ID, DeleteOrder , PAGTO, ENTREGA, ORDERS, SETORDERS}) {
  const classes = useStyles();

  const selectStatus = (STATUS, OCORRENCIA) => {

    switch (OCORRENCIA) {
      case '51':
        return <Button style={{ background: 'darkorange', color: '#FFF' }}>{STATUS}</Button>
      case '52':
        return <Button style={{ background: 'darkred', color: '#FFF' }}>{STATUS}</Button>
      case '53':
        return <Button style={{ background: 'darkblue', color: '#FFF' }}>{STATUS}</Button>
      case '54':
        return <Button style={{ background: 'darkgreen', color: '#FFF' }}>{STATUS}</Button>
      default:
        return <Button style={{ background: '#312e38', color: '#FFF' }}>{STATUS}</Button>
    }
  }

  return (
    <Dialog itens={ITENS} total={TOTAL} cnpj={CNPJ} pedido={NUMERO} status={OCORRENCIA} id={ID} DeleteOrder={DeleteOrder} pagamento={PAGTO} entrega={ENTREGA} orders={ORDERS} setOrders={SETORDERS}>
      <Card className={classes.root}>
        <CardContent>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 20 }}>
            <Typography className={classes.title} color="textSecondary" gutterBottom>
              {format(new Date(EMISSAO), 'dd/MM/yyyy')}
            </Typography>
            <Typography variant="h5" component="h2">
              {NUMERO}
            </Typography>
          </div>
          <Typography className={classes.pos} variant="h5" component="h2" color="textSecondary">
            {CNPJ}
          </Typography>
          <Typography className={classes.pos} color="textSecondary">
            {CAPELIDO}
          </Typography>

          {selectStatus(STATUS, OCORRENCIA)}

        </CardContent>
      </Card>
    </Dialog>
  );
}
