import styled, { keyframes } from 'styled-components';
import { shade } from 'polished';

/* import SignInBAckground from '../../assets/feira.jpg'; */

const appearFromLeft = keyframes`
from {
  opacity: 0;
  transform: translateX(-50px);
}
to{
  opacity: 1;
  transform: translateX(0);
}
`;

export const Container = styled.div`
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 10px;
`;

export const Content = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex-direction: column;

  animation: ${appearFromLeft} 1s;

  img {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  form {
    margin: 50px 0;
    width: 300px;
    text-align: center;
    h1 {
      margin-bottom: 24px;
      color: #dc6a48;
    }
    a {
      color: #f4edeb;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 15px;
      text-decoration: none;
      transition: color 0.2s;

      svg {
        color: #dc6a48;
        margin-right: 8px;
      }

      &:hover {
        color: ${shade(0.2, '#f4edeb')};
      }
    }

    > div + div {
      margin-top: 8px;
    }

    button {
      margin-top: 8px;
    }
  }
  > a {
    color: #dc6a48;
    display: flex;
    margin-top: 24px;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    transition: color 0.2s;
    svg {
      color: #dc6a48;
      margin-right: 16px;
    }
    &:hover {
      color: ${shade(0.2, '#dc6a48')};
    }
  }
`;

export const AnimationContent = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  animation: ${appearFromLeft} 1s;

  form {
    margin: 80px 0;
    width: 300px;
    text-align: center;
    h1 {
      margin-bottom: 24px;
      color: #dc6a48;
    }
    a {
      color: #f4edeb;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 15px;
      text-decoration: none;
      transition: color 0.2s;

      svg {
        color: #dc6a48;
        margin-right: 8px;
      }

      &:hover {
        color: ${shade(0.2, '#f4edeb')};
      }
    }

    > div + div {
      margin-top: 8px;
    }

    button {
      margin-top: 8px;
    }
  }
  > a {
    color: #dc6a48;
    display: block;
    margin-top: 24px;
    text-decoration: none;
    display: flex;
    align-items: center;
    transition: color 0.2s;
    svg {
      color: #dc6a48;
      margin-right: 16px;
    }
    &:hover {
      color: ${shade(0.2, '#dc6a48')};
    }
  }
`;

