import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useToast } from '../../hooks/Toast';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import CardTable from '../CardTable';
import { api } from '../../services/api';

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
    background: '#312E38',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog({ children, itens, pedido, total, status, id, DeleteOrder, cnpj, pagamento, entrega, orders, setOrders }) {
  const token = localStorage.getItem('@Monarca: token');
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const { addToast } = useToast();

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleSend = () => {
    api.put(`/orders/approve`, { id }, {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })
      .then(({ data }) => {
        addToast({
          type: 'success',
          title: 'Sucesso',
          description:
            data.msg,
        });
        const newOrders = orders.map(val => {
          if (val.ID === id) {
            val.OCORRENCIA = '51';
            val.STATUS = 'EM ANLISE';
          }
          return val
        })
        setOrders(newOrders)
      })
      .catch(() => {
        addToast({
          type: 'error',
          title: 'Erro',
          description:
            'Falha ao efetivar o pedido.',
        });
      })
      .finally(() => {
        setOpen(false);
      })
  }

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <div onClick={handleClickOpen}>
        {children}
      </div>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition} >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Itens do pedido
            </Typography>
            {(status === '50' || status === '52') && <Button autoFocus color="inherit" onClick={handleSend}>
              Efetivar
            </Button>
            }
          </Toolbar>
        </AppBar>

        <CardTable itens={itens} pedido={pedido} cnpj={cnpj} total={total} status={status} id={id} handleClose={handleClose} DeleteOrder={DeleteOrder} pagamento={pagamento} entrega={entrega} />

      </Dialog>
    </>
  );
}
