import styled, { keyframes } from 'styled-components';
import { shade } from 'polished';

const appearFromLeft = keyframes`
from {
  opacity: 0;
  transform: translateX(-50px);
}
to{
  opacity: 1;
  transform: translateX(0);
}
`;

export const Container = styled.div`
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 10px;
`;

export const Content = styled.div`
display: flex;
justify-content: center;
align-items: center;
margin: 0 auto;
flex-direction: column;

animation: ${appearFromLeft} 1s;

img {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
}

form {
  margin: 50px 0;
  width: 300px;
  text-align: center;
  h1 {
    margin-bottom: 24px;
    color: #dc6a48;
  }
  a {
    color: #f4edeb;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    text-decoration: none;
    transition: color 0.2s;

    svg {
      color: #dc6a48;
      margin-right: 8px;
    }

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }
  }

  > div + div {
    margin-top: 8px;
  }

  button {
    margin-top: 8px;
  }
}
> a {
  color: #dc6a48;
  display: flex;
  margin-top: 24px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  transition: color 0.2s;
  svg {
    color: #dc6a48;
    margin-right: 16px;
  }
  &:hover {
    color: ${shade(0.2, '#dc6a48')};
  }
}
`;

export const Header = styled.div`
  display: flex;
  align-items: center;

  @media (min-width: 500px) {
    justify-content: center;
  }

  justify-content: center;
`;

export const Menu = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 500px) {
    display: none;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: stretch;

    color: #f4edeb;
    cursor: pointer;
    text-decoration: none;
    width: 115px;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }

  button {
    display: flex;
    align-items: center;
    justify-content: stretch;

    width: 90px;
    height: 36px;
    margin-left: 10px;

    &:hover {
      background: ${shade(0.2, '#dc6a48')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const Grid = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const Card = styled.div`
  display: flex;
  flex-direction: column;

  height: 60px;
  border: 1px solid #dc6a48;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
`;

export const SpanTitle = styled.span`
  color: #dc6a48;
  font-size: 18px;
`;

export const Itens = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    cursor: pointer;
  }
`;

export const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  a {
    color: #f4edeb;
    text-decoration: none;
    display: flex;
    align-items: center;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const BottomMenu = styled.div`
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #dc6a48;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 8px min(46px, (max(10vw, 10px)));

  @media (min-width: 500px) {
    display: none;
  }

  a {
    text-decoration: none;
    svg {
      color: #f4edeb;
      cursor: pointer;

      &.active {
        color: #dc6a48;
      }
    }
  }

  > svg {
    color: #f4edeb;
    cursor: pointer;
  }
`;
