import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Toolbar, IconButton, Typography } from '@material-ui/core';
import { TextField, Dialog, Button } from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';
import CloseIcon from '@material-ui/icons/Close';
import { AddProducts } from './styles';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '94vw',
  },
  cardRoot: {
    maxHeight: '50vh',
    cursor: 'pointer',
  },
  cardContent: {
    maxHeight: '50vh',
    overflowX: 'scroll',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  list: {
    width: '100%',
  },
  listItem: {
    listStyleType: 'none',
  },
  appBar: {
    position: 'relative',
    background: '#312E38',
  },
}));

interface comboItens {
  IDPRODUTO: string,
  DESCPRD: string,
  VALOR: number,
}

interface Item {
  ID: number,
  IDPRODUTO: string,
  DESCPRD: string,
  QUANTIDADE: number,
  VALOR: number,
}

interface EditItem {
  quantidade: number;
  idProduto: string;
}

interface Itens {
  openAdd: boolean,
  editItem?: EditItem;
  itens: Array<Item>,
  comboItens: Array<comboItens>,
  total: number;
  setOpenAdd: (open: boolean) => void,
  setItens: (itens: Array<Item>) => void,
  setTotal: (total: number) => void;
  setEditItem?: (value: EditItem) => void;
}

const AddItem: React.FC<Itens> = ({ openAdd, setOpenAdd, itens, setItens, comboItens, total, setTotal, editItem, setEditItem }) => {
  const classes = useStyles();
  const [id, setId] = React.useState(0)
  const [idProduto, setIdproduto] = React.useState('')
  const [produto, setProduto] = React.useState('')
  const [quantidade, setQuantidade] = React.useState(0)
  const [valor, setValor] = React.useState(0)

  useEffect(() => {
    if (editItem) {
      const [itemSelected] = itens.filter(value => value.IDPRODUTO === editItem?.idProduto);

      if (itemSelected) {
        setId(itemSelected.ID);
        setIdproduto(itemSelected.IDPRODUTO);
        setValor(itemSelected.VALOR);
        setProduto(itemSelected.DESCPRD);
        setQuantidade(editItem.quantidade);
      }
    }
  }, [editItem, itens])

  const handleOpenAdd = () => {
    setQuantidade(0);
    setProduto('');
    setIdproduto('');
    setValor(0);
    if (setEditItem) {
      setEditItem({
        idProduto: '',
        quantidade: 0,
      })
    }
    openAdd ? setOpenAdd(false) : setOpenAdd(true);
  }

  const handleSubmit = (e: any) => {

    const filtered = itens.filter(value => value.IDPRODUTO === idProduto);

    if (filtered.length > 0) {
      const newItens = itens.map(value => {
        if (value.IDPRODUTO === idProduto) {
          if (editItem?.idProduto) {
            value.QUANTIDADE = quantidade;
          } else {
            value.QUANTIDADE += quantidade;
          }
        }
        return value;
      })

      setItens(newItens);
    } else {

      setItens([
        ...itens,
        {
          ID: id,
          IDPRODUTO: idProduto,
          DESCPRD: produto,
          QUANTIDADE: quantidade,
          VALOR: valor
        }
      ]);
    }
    setTotal((quantidade * valor) + total);
    setProduto('');
    setOpenAdd(false);
    setQuantidade(0);
    setValor(0);
    if (setEditItem) {
      setEditItem({
        idProduto: '',
        quantidade: 0,
      })
    }
  }

  const handleChangeProducts = (e: any, value: any) => {
    if (value) {
      setId(value.ID);
      setIdproduto(value.IDPRODUTO);
      setProduto(value.DESCPRD);
      setValor(value.VALOR);
    }
    setQuantidade(0);
  }

  const handleChangeQuantidade = (e: any) => {
    let input = e.target.value;
    input = input
      .replace(/\D/g, '')
      .replace(/(\d{8})\d+?$/, '$1');

    e.target.value = input

    if (!input) {
      setQuantidade(0)
    } else {
      setQuantidade(parseInt(input))
    }
  }

  return (
    <div className={classes.root}>
      <Dialog fullScreen open={openAdd} >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleOpenAdd} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Adicionar item
            </Typography>
          </Toolbar>
        </AppBar>

        <div>
          <AddProducts >
            <Autocomplete
              id="combo-box"
              options={comboItens}
              getOptionLabel={(option) => option.DESCPRD}
              inputValue={produto}
              renderInput={(params) => <TextField {...params} size="small" label="Produto" variant="outlined" />}
              onChange={handleChangeProducts}
            />
            <TextField
              label="Quantidade"
              type="text"
              variant="outlined"
              size="small"
              placeholder="0"
              value={quantidade}
              onChange={(e) => handleChangeQuantidade(e)}
            />
            <TextField
              disabled
              label="Valor Unitrio"
              variant="outlined"
              value={new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(valor)}
              size="small" />

            <TextField
              disabled
              label="Valor Total"
              size="small"
              variant="outlined"
              value={new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(valor * quantidade)}
            />
            <Button className="button" variant="contained" disabled={(produto.length === 0 || quantidade === 0)} color="primary" onClick={handleSubmit}>
              Salvar
            </Button>
          </AddProducts>
        </div>
      </Dialog>
    </div>
  );
}

export default AddItem;
