import styled from 'styled-components';
import { shade } from 'polished';

export const Container = styled.div`
  display: flex;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
`;

export const Content = styled.div`
  padding: 10px;
  margin: 0 auto;
  flex: 1;
`;

export const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  //margin-right: 10px;


  padding: 10px;
  > img {
    width: 150px;
  }

  > div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 340px;
    margin-right: 5px;
  }

  @media (max-width: 500px) {
    flex-direction: column;
    justify-content: center;
    height: 'auto';
  }

`;

export const Menu = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 500px) {
    display: none;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: stretch;

    color: #f4edeb;
    cursor: pointer;
    text-decoration: none;
    width: 115px;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }

  button {
    display: flex;
    align-items: center;
    justify-content: stretch;

    width: 90px;
    height: 36px;
    margin-left: 10px;

    &:hover {
      background: ${shade(0.2, '#dc6a48')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const Grid = styled.div`
  display: flex;
  align-items: center;
  padding: 10px;
  flex-direction: column;
  background: #484545;
  height: 80vh;
  overflow-y: scroll;
`;

export const Asside = styled.div`

  max-width: 1400px;
  display: flex;
  align-items: center;
  justify-content: center;


  flex-wrap: wrap;

  /*  border: 2px solid tomato;
  border-radius: 4px; */
  /* background: tomato; */
  /*height: 84vh;*/

  /* > span {
    margin-top: 10px;
  } */
 /* > div.card {
    max-width: 326px;
    margin: 5px;
  }*/

  @media only screen and (max-width: 635px) {
    flex-direction: column;
    max-width: '100%';
    margin-right: 0;

    > div {
      max-width: 340px;
      margin: 5px;
    }
  }


`;

export const Card = styled.div`
  display: flex;
  flex-direction: column;

  height: 60px;
  border: 1px solid #dc6a48;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
`;

export const Option = styled.span`
  width: 520px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  > a {
    text-decoration: none;

    & button{
      display: block;
    }

    & button + button{
      display: none;
    }
  }

  > span + span {
    margin-right: 50px;
  }


  @media (max-width: 500px) {
    width: 100%;

    > span + span {
      margin-right: 0;
    }

    > a {
      & button{
        display: none;
      }

      & button + button{
        display: block;
      }
    }
  }
  > span + span{
    display: flex;
    cursor: pointer;
  }
  `;

export const DivSearch = styled.span`
  width: 250px;
`;

export const SpanTitle = styled.span`
  color: #dc6a48;
  font-size: 18px;
`;

export const Itens = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    cursor: pointer;
  }
`;

export const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  a {
    color: #f4edeb;
    text-decoration: none;
    display: flex;
    align-items: center;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const Cards = styled.div`
  display: flex;
  flex-direction: column;
  max-height: 66vh;
  overflow-y: scroll;

  @media only screen and (max-width: 1366px) {
    height: 55vh;
  }

  @media only screen and (max-width: 414px) {
    height: 48vh;
  }

  @media only screen and (max-width: 411px) {
    height: 49vh;
  }

  @media only screen and (max-width: 375px) {
    height: 43vh;
  }

  @media only screen and (max-width: 360px) {
    height: 44vh;
  }

  @media only screen and (max-width: 320px) {
    height: 38vh;
  }
`;

export const BottomMenu = styled.div`
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #dc6a48;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 8px min(46px, (max(10vw, 10px)));

  @media (min-width: 500px) {
    display: none;
  }

  a {
    text-decoration: none;
    svg {
      color: #f4edeb;
      cursor: pointer;

      &.active {
        color: #dc6a48;
      }
    }
  }

  > svg {
    color: #f4edeb;
    cursor: pointer;
  }
`;

