import styled, { css } from 'styled-components';

interface ContainerProps {
  isFocused?: boolean;
  isFilled?: boolean;
  isErrored?: boolean;
  checked?: boolean;
  disabled?: boolean;
}

export const ContainerInput = styled.div<ContainerProps>`
  background: #232129;
  border-radius: 10px;
  padding: 12px;
  width: 100%;

  border: 2px solid #232129;
  color: #666360;

  display: flex;
  align-items: center;

  margin-top: 20px;

  ${(props) =>
    props.isFocused &&
    css`
      border: 2px solid #dc6a48;
      color: #dc6a48;
    `}

  ${(props) =>
    (props.isFilled || props.checked) &&
    css`
      color: #dc6a48;
    `}

    input {
      flex: 1;
      width: 100%;
      border: 0;
      background: transparent;
      color: #f4edeb;

      &::placeholder {
        color: #666360;
      }
    }

  div {
    flex: 1;
    border: 0;
    background: transparent;
    color: #f4edeb;

    &::placeholder {
      color: #666360;
    }
  }

  ${(props) =>
    props.disabled &&
    css`
      input,
      svg {
        color: #666360;
        cursor: not-allowed;
      }
    `}

  svg {
    margin-right: 16px;
  }
`;

export const ContainerCard = styled.div<ContainerProps>`
  width: 100%;
  max-height: 60vh;
  overflow: scroll;

  li {
    list-style-type: none;
  }

  div {
    marginBottom: 20px;
  }

  :hover {
    cursor: pointer;
  }

  .table {
    overflow-x: hidden;
  }
`

export const ContainerDialog = styled.div<ContainerProps>`
  width: 100%;
  font-size: 40px;

  .app-bar {
    background: #312E38;
  }
`
