import React, { useCallback, useRef, useState } from 'react';
import { FiMail, FiArrowLeft } from 'react-icons/fi';
import { Link, useHistory } from 'react-router-dom';
import { FormHandles } from '@unform/core';
import * as Yup from 'yup';
import { Form } from '@unform/web';
import getValidantionErrors from '../../utils/getValidationErrors';
import { api } from '../../services/api';

import { useToast } from '../../hooks/Toast';

import { Container, Content, AnimationContent } from './styles';

import Button from '../../components/Button';
import Input from '../../components/Input';
import logo from '../../assets/logo.png';

interface SignInFormData {
  email: string;
}

const Forgot: React.FC = () => {
  const formRef = useRef<FormHandles>(null);
  const [disabled, setDisabled] = useState(false);

  const { addToast } = useToast();

  const history = useHistory();

  const handleSubmit = useCallback(
    async (data: SignInFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          email: Yup.string()
            .required('Email  obrigatrio')
            .email('Email invlido'),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        setDisabled(true);

        addToast({
          type: 'info',
          title: 'Enviando email!',
          description:
            'Em instantes voc receber um email com um token de autrorizao.',
        });

        await api.post('login/forgot', data);

        addToast({
          type: 'success',
          title: 'Email enviado com sucesso!',
          description: 'O email foi enviado, verifique sua caixa de entrada.',
        });

        history.push('/recovery');
      } catch (err) {
        setDisabled(false);
        if (err instanceof Yup.ValidationError) {
          const errors = getValidantionErrors(err);
          formRef.current?.setErrors(errors);

          return;
        }

        addToast({
          type: 'info',
          title: 'Erro ao enviar email',
          description:
            'Ocorreu um erro ao enviar o email, tente novamente mais tarde.',
        });
      }
    },
    [addToast, history],
  );
  return (
    <Container>
      <Content>
        <AnimationContent>
          <img src={logo} alt="Logo HTM" />
          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Recuperar senha</h1>

            <Input name="email" icon={FiMail} placeholder="E-mail" />

            <Button name="button" type="submit" disabled={disabled}>
              Enviar
            </Button>
          </Form>

          <Link to="/">
            <FiArrowLeft />
            Voltar
          </Link>
        </AnimationContent>
      </Content>
    </Container>
  );
};

export default Forgot;
