import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useToast } from '../../hooks/Toast';
import { api } from '../../services/api';

interface IRecipeProps {
  children: React.ReactNode;
  id: number;
  handleClose(): boolean;
  DeleteOrder(id: number): number;
}

const AlertDialog: React.FC<IRecipeProps> = ({ children, id, handleClose, DeleteOrder }) => {
  const [open, setOpen] = React.useState(false);
  const token = localStorage.getItem('@Monarca: token');
  const { addToast } = useToast();

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClosed = () => {
    setOpen(false);
  };

  const handleDelete = () => {


    api.delete('/orders/delete', {
      data: { id }, headers: {
        Authorization: `Bearer ${token}`,
      }
    },
    ).then(() => {
      DeleteOrder(id);
      handleClosed();
      handleClose();
      addToast({
        title: 'Excluso de pedido',
        description:
          'O Pedido foi excludo com sucesso.',
        type: 'success',
      });

    })
      .catch(() => {
        addToast({
          title: 'Excluso de pedido',
          description:
            'No foi possvel excluir o pedido.',
          type: 'error',
        });
      })
  };

  return (
    <div>
      <div onClick={handleClickOpen} >
        {children}
      </div>
      <Dialog
        open={open}
        onClose={handleClosed}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Deseja excluir o pedido?</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Esta ao no poder ser revertida.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClosed} color="primary">
            No
          </Button>
          <Button onClick={handleDelete} color="primary" autoFocus>
            Sim
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default AlertDialog;
