import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../../hooks/Auth';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import { Table, TableHead, TableCell, TableRow, TableBody } from '@material-ui/core';
import Modal from '../Modal';
import { format } from 'date-fns'

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100vw',
    backgroundColor: theme.palette.background.paper,
    color: '#000',
  },
  chip: {
    margin: theme.spacing(0.5),
  },
  section1: {
    margin: theme.spacing(3, 2),
  },
  section2: {
    margin: theme.spacing(2),
    overflowX: 'scroll',
  },
  section3: {
    width: 300,
    marginTop: 30,
    margin: theme.spacing(3, 1, 1),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
  },
  table: {
    overflowX: 'scroll',
  }
}));

export default function MiddleDividers({ itens, pedido, total, status, id, handleClose, DeleteOrder, cnpj, pagamento, entrega }) {
  const classes = useStyles();
  const history = useHistory();
  const { editOrder } = useAuth();

  const handleOrder = () => {
    editOrder({ cpfCnpj: cnpj, pedido, itens, status, id, pagamento, entrega });
    history.push('order');
  }
  return (
    <div className={classes.root}>
      <div className={classes.section1}>
        <Grid container alignItems="center">
          <Grid item xs>
            <Typography gutterBottom variant="h4">
              {pedido}
            </Typography>
          </Grid>
          <Grid item>
            <Typography gutterBottom variant="h6">
              {new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(total)}
            </Typography>
          </Grid>
        </Grid>
        <Typography color="textSecondary" variant="body2">
          Visualizar itens do pedido
        </Typography>
      </div>
      <Divider variant="middle" />

      <div className={classes.section2}>
        <div style={{ marginTop: 10 }}>Forma de Pagamento: {pagamento.DESCRICAO}</div>
        <div style={{ marginBottom: 10 }}>Data de Entrega: {format(new Date(entrega), 'dd/MM/yyyy')}</div>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>#</TableCell>
              <TableCell align="left">Produto</TableCell>
              <TableCell align="left">Descrio</TableCell>
              <TableCell align="left">Quantidade</TableCell>
              <TableCell align="left">Valor unitrio</TableCell>
              <TableCell align="left">Total</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {itens.map((row, key) => (
              <TableRow key={key}>
                <TableCell component="th" scope="row">
                  {row.IDITEM}
                </TableCell>
                <TableCell align="left">{row.IDPRODUTO}</TableCell>
                <TableCell align="left">{row.DESCPRD}</TableCell>
                <TableCell align="left">{row.QTDADE}</TableCell>
                <TableCell align="left">{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VR_UNITARIO)}</TableCell>
                <TableCell align="left">{new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(row.VR_TOTAL)}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
      { (status === '50' || status === '52') &&
        <div className={classes.section3}>
          <Button variant="contained" style={{ background: "#312E38", marginRight: 10 }} onClick={handleOrder} color="secondary" size="small">EDITAR</Button>
          <Modal id={id} handleClose={handleClose} DeleteOrder={DeleteOrder}>
            <Button variant="contained" style={{ background: "#f08323", color: "#FFF" }} color="default" size="small">EXCLUIR</Button>
          </Modal>
        </div>
      }
    </div>
  );
}
