import styled from 'styled-components';
import { shade } from 'polished';

export const Container = styled.div`
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
`;

export const Content = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 auto;
  /* width: 340px; */
  height: 100%;

  form {
    width: 100%;
    padding: 10px;
    > div + div {
      margin-top: 8px;
    }
  }

  @media (max-width: 500px) {
    flex-direction: column;
  }
`;

export const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;


  padding: 10px;
  > img {
    width: 150px;
  }

  > div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 340px;
    margin-right: 5px;
  }
`;



export const Menu = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 500px) {
    display: none;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: stretch;

    color: #f4edeb;
    cursor: pointer;
    text-decoration: none;
    width: 115px;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }

  button {
    display: flex;
    align-items: center;
    justify-content: stretch;

    width: 90px;
    height: 36px;
    margin-left: 10px;

    &:hover {
      background: ${shade(0.2, '#dc6a48')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const Grid = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin: 0 auto;
  flex-direction: column; */
  padding: 10px;
    > div + div {
      margin-top: 8px;
    }
`;

export const Card = styled.div`
  display: flex;
  flex-direction: column;

  height: 60px;
  border: 1px solid #dc6a48;
  border-radius: 4px;
  margin-top: 10px;
  padding: 10px;
`;

export const SpanTitle = styled.span`
  color: #dc6a48;
  font-size: 18px;
`;

export const Itens = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  svg {
    cursor: pointer;
  }
`;

export const Buttons = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  a {
    color: #f4edeb;
    text-decoration: none;
    display: flex;
    align-items: center;

    &:hover {
      color: ${shade(0.2, '#f4edeb')};
    }

    svg {
      margin-right: 5px;
    }
  }
`;

export const BottomMenu = styled.div`
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #dc6a48;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 8px min(46px, (max(10vw, 10px)));

  @media (min-width: 500px) {
    display: none;
  }

  a {
    text-decoration: none;
    svg {
      color: #f4edeb;
      cursor: pointer;

      &.active {
        color: #dc6a48;
      }
    }
  }

  > svg {
    color: #f4edeb;
    cursor: pointer;
  }
`;

export const ContainerButtons = styled.div`
  display: flex;
  flex-flow: row;
  justify-content: space-between;

  .containerButton {
    width: 250px;
  }

  @media screen and (max-width: 1440px) {
    .containerButton {
      width: 200px;
    }
  }

  @media screen and (max-width: 500px) {
    .containerButton {
      width: 100px;
    }
  }

  @media screen and (max-width: 500px) {
    .containerButton {
      width: 70px;
    }
  }

`
export const ContainerCheckout = styled.div`
  scroll-x: scroll;
  heigth: 100%;

  .textField {
    margin-top: 8px;
    width: 100%;
    // backgorund: #333;
  }
  .textField > div {
    background: white;
  }

  .containerName {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
  }

  .containerButtons {
    // height: 64vh;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }

  .button {
    width: 250px;
    margin-top: 8px;
  }

  @media screen and (max-width: 1024px) {
    .button {
      width: 200px;
    }
  }

  @media screen and (max-width: 425px) {
    .button {
      width: 150px;
    }
  }

  @media screen and (max-width: 320px) {
    // .containerButtons {
    //   height: 58vh;
    // }

    .button {
      width: 120px;
    }
  }

`

