import React, { useEffect, useState, useCallback } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { FiSearch, FiPlus, FiLogOut } from 'react-icons/fi';
import { format } from 'date-fns';
import { useAuth } from '../../hooks/Auth';
import { useToast } from '../../hooks/Toast';
import { api } from '../../services/api';
import Card from '../../components/Card';
import Search from '../../components/Search';
import Button from '../../components/Button';

import {
  Container,
  Header,
  Grid,
  Asside,
  Option,
  DivSearch
} from './styles';
import logo from '../../assets/NewLogoWhite.png';

interface Items {
  IDITEM?: number;
  IDPRODUTO?: number;
  DESCPRD?: string;
  QTDADE?: number;
  VR_UNITARIO?: number;
  VR_TOTAL?: number;
}

interface Order {
  ID: number;
  NUMERO: number;
  EMISSAO: string;
  CAPELIDO: string;
  STATUS: string;
  CCNPJ: string;
  OCORRENCIA: string;
  TOTAL: number;
  ITENS: Array<Items>
  IDFORMAPAGTO: number;
  DESCFORMAPGTO: string;
  ENTREGA: string;
}

const Dashboard: React.FC = () => {
  const history = useHistory();
  const { signOut } = useAuth();
  const { addToast } = useToast();
  const [orders, setOrders] = useState<Order[]>([]);
  const [search, setSearch] = useState<Order[]>([]);
  const [notFound, setNotFound] = useState(false);

  const token = localStorage.getItem('@Monarca: token');

  useEffect(() => {

    api.get('orders/list', {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then(({ data }) => {
        setOrders(data.orders);
        setSearch(data.orders);
        setNotFound(true);
      })
      .catch(() => {
        addToast({
          title: 'Token expirado',
          description:
            'O token de acesso expirou,  necessrio fazer login novamente.',
          type: 'info',
        });
        signOut();
      });
  }, [signOut, token, addToast, orders]);

  const logout = useCallback(() => {
    signOut();
    history.push('/');
  }, [signOut, history]);

  const filterData = (filter: string): void => {

    const filterLowerCase = filter.toLowerCase();

    const orderFiltered = orders.filter((value) => {
      return value.CAPELIDO.toLowerCase().includes(filterLowerCase)
        || value.OCORRENCIA.toLowerCase().includes(filterLowerCase)
        || value.STATUS.toLowerCase().includes(filterLowerCase)
        || value.NUMERO.toString().includes(filterLowerCase)
        || value.CCNPJ.toString().includes(filterLowerCase)
        || format(new Date(value.EMISSAO), 'dd/MM/yyyy').includes(filterLowerCase)
    });
    setSearch(orderFiltered)
  }

  const DeleteOrder = (id: number) => {
    const newOrders = orders.filter((value) => value.ID !== id);
    setSearch(newOrders);
  }
  return (
    <>
      <Container>

        <Header>
          <img src={logo} alt="logo Monarca" />
          <Option >
            <Link to="/order">
              <Button style={{ maxWidth: 280 }}>Cadastrar pedido</Button>
              <Button><FiPlus /></Button>
            </Link>
            <DivSearch>
              <Search name="search" icon={FiSearch} placeholder="Pesquisar" setOrders={filterData} />
            </DivSearch>
            <span onClick={logout}>
              <FiLogOut style={{marginLeft: 5, marginRight: 5}}/> Sair
            </span>
          </Option>
        </Header>
        <Grid>
          <Asside>
            {
              search.length > 0 ?
                search.map(({ ID, NUMERO, EMISSAO, CAPELIDO, STATUS, ITENS, TOTAL, OCORRENCIA, CCNPJ, IDFORMAPAGTO, DESCFORMAPGTO, ENTREGA }) => {
                  return <Card
                    key={ID}
                    ID={ID}
                    NUMERO={NUMERO === 0 ? '-' : NUMERO}
                    EMISSAO={EMISSAO}
                    CAPELIDO={CAPELIDO}
                    STATUS={STATUS}
                    ITENS={ITENS}
                    TOTAL={TOTAL}
                    OCORRENCIA={OCORRENCIA}
                    CNPJ={CCNPJ}
                    PAGTO={{ ID: IDFORMAPAGTO, DESCRICAO: DESCFORMAPGTO }}
                    DeleteOrder={DeleteOrder}
                    ENTREGA={ENTREGA}
                    ORDERS={orders}
                    SETORDERS={setOrders}
                  />
                }
                )
                : notFound ? <span> No possui pedidos...</span> : <span> Buscando pedidos...</span>
            }

          </Asside>
        </Grid>
      </Container>
    </>
  );
};

export default Dashboard;
