import styled, { css } from 'styled-components';

import Tooltip from '../Tooltip';

interface ContainerProps {
  isFocused: boolean;
  isFilled: boolean;
  isErrored?: boolean;
  checked?: boolean;
  disabled?: boolean;
}

export const Container = styled.div<ContainerProps>`
  background: #232129;
  border-radius: 10px;
  padding: 12px;
  width: 100%;

  border: 2px solid #232129;
  color: #666360;

  display: flex;
  align-items: center;

  ${(props) =>
    props.isErrored &&
    css`
      border: 2px solid #c53030;
    `}

  ${(props) =>
    props.isFocused &&
    css`
      border: 2px solid #dc6a48;
      color: #dc6a48;
    `}

  ${(props) =>
    (props.isFilled || props.checked) &&
    css`
      color: #dc6a48;
    `}


  input {
    flex: 1;
    border: 0;
    background: transparent;
    color: #f4edeb;

    &::placeholder {
      color: #666360;
    }
  }

  ${(props) =>
    props.disabled &&
    css`
      input,
      svg {
        color: #666360;
        cursor: not-allowed;
      }
    `}

  svg {
    margin-right: 16px;
  }
`;

export const Error = styled(Tooltip)`
  height: 20px;
  margin-left: 16px;
  cursor: pointer;
  svg {
    margin: 0;
  }

  span {
    background: #c53030;
    color: #f4edeb;

    &::before {
      border-color: #c53030 transparent;
    }
  }
`;
